import React, { useState } from "react";
import { Box, Tabs, Tab, Heading, Button, TextInput } from "grommet";
import { Facebook, Tools } from "grommet-icons";

import Parse, { signUp, resetPassword } from "../library/parse";
const Component = ({ onClose }) => {
  const [model, setModel] = useState({});
  const [message, setMessage] = useState("");
  const [mode, setMode] = useState("Login");
  return (
    <Box>
      <Heading level={3}> Please login to Bid</Heading>
      Email:
      <TextInput
        value={model["email"]}
        onChange={(event) => {
          setModel({
            ...model,
            username: event.target.value,
            email: event.target.value,
          });
        }}
        placeholder="email"
      />
      <Button
        label="Reset Password"
        primary
        onClick={() => {
          if (model.email) {
            resetPassword(model.email).then((e) =>
              setMessage("A reset email is sent")
            );
          }
        }}
      />
      {message}
    </Box>
  );
};
export default Component;
